@use "global" as *;

.news__wrapper{
    @include mq(md){
        display: flex;
        max-width: rem(1040);
        width: 100%;
        margin: auto;
        align-items: center;
    }
}

.news__header{
    @include mq(md){
        width: 23%;
        margin-right: 4.5%;
    }
}

.news__date{
    margin-right: rem(27);
    @include mq(md){
        margin-right: rem(43);
    }
}

.news__tag{
    display: inline-block;
    font-size: rem(14);
    padding: rem(5) rem(22);
    border: solid 1px $white;
    @include mq(md){
        padding: rem(4) rem(21);
    }
}

.news__text{
    margin-top: rem(15);
    border-bottom: solid 1px transparent;
    @include mq(md){
        width: 56%;
        margin-top: 0;
        margin-right: 4%;
    }
}

.news__text:hover{
    @include mq(md){
        border-bottom: solid 1px $white;
        cursor: pointer;
    }
}

.news__btn{
    margin-top: rem(26);
    @include mq(md){
        margin-top: 0;
        }
}
