@use "global" as *;

.sub-contact{
  margin-top: rem(85);
  margin-bottom: rem(120);
  @include mq(md) {
    margin-top: rem(115);
    margin-bottom: rem(150);
  }
}

.sub-contact__inner{
  @include mq(md) {
    max-width: calc(#{rem(700)} + #{rem(25)});
  }
}

.sub-contact__form{
  font-size: rem(14);
}

.sub-contact__item{
  @include mq(md) {
    display: flex;
  }
}

.sub-contact__item:not(:first-child){
  @include mq(md) {
    margin-top: rem(29);
  }
}

.sub-contact__item:not(:last-child){
  @include mq(md) {
    align-items: center;
  }
}

.sub-contact__item .smf-item__col.smf-item__col--label{
  @include mq(md) {
    width: rem(132);
    margin-right: rem(24);
    text-align: right;
    flex-shrink: 0;
  }
}

.smf-item__col.smf-item__col--controls{
  width: 100%;
}

.sub-contact__item .smf-item__col.smf-item__col--label{
  padding-bottom: 0;
}

.sub-contact__item .smf-text-control__control.sub-contact__controls{
  border-radius: 0;
  margin-top: rem(16);
  @include mq(md) {
    margin-top: 0;
  }
}

.sub-contact__item .smf-textarea-control__control.sub-contact__controls{
  border-radius: 0;
  margin-top: rem(16);
  @include mq(md) {
    margin-top: 0;
  }
}

.sub-contact .smf-action .smf-button-control__control{
  border-radius: 0;
  font-size: rem(16);
  width: 100%;
  @include mq(md) {
    width: rem(360);
    height: rem(60);
    margin-left: 20%;
    margin-top: rem(28);
  }
}

.sub-contact .smf-action .smf-button-control+.smf-button-control{
  margin-left: 0;
}

.sub-contact__text{
  margin-top: rem(80);
  font-size: rem(20);
  letter-spacing: 0.02em;
  text-align: center;
  @include mq(md) {
    margin-top: rem(150);
    font-size: rem(30);
    line-height: calc(35 / 30);
  }
}

.sub-contact__text:nth-child(n + 1){
  @include mq(md) {
    margin-top: rem(80);
  }
}

.sub-contact__gototop{
  margin-top: rem(80);
  font-size: rem(20);
    @include mq(md){
        margin: rem(80) auto 0;
    }
}