@use "global" as *;

.pagetop{
    position: fixed;
    right: rem(14);
    bottom: rem(35);
    z-index: 5;
    @include mq(md){
        right: rem(25);
        bottom: rem(28);
    }
}

.arrow{
    display:  inline-block;
    width: rem(45);
    height: rem(45);
    color: $gray;
    background: $white;
    position:  relative;
    transition: 0.3s;
}

.arrow:hover{
    @include mq(md){
        background: $gray;
        color: $white;
    }
}

.arrow::before {
    content:  '';
    width: rem(20);
    height: rem(20);
    display:  block;
    border-top: solid 2px;
    border-right: solid 2px;
    transform: rotate(-45deg);
    position:  absolute;
    top: 10px;
    bottom:  0;
    left:  0;
    right:  0;
    margin:  auto;
}