@use "global" as *;

.header{
    height: rem(50);
    padding: 0 rem(22);
    background: rgba(17, 17, 17, 0.5);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;
    transition: 0.3s;
    @include mq(md){
        padding: 0 rem(32);
    }
}

.header.opacity1{
    background: $black;
}

.header__inner{
    display: flex;
    justify-content: space-between;
    height: inherit;
}

.header__logo{
    width: rem(71);
    height: inherit;
    margin-top: rem(2);
    z-index: 15;
    @include mq(md){
        width: rem(94);
    }
}

.header__logo a{
    height: inherit;
    display: flex;
    align-items: center;
}

.header__hamburger{
    width: rem(26);
    padding: 0;
    position: relative;
    z-index: 20;
}

.header__hamburger span{
    height: rem(2);
    display: block;
    border-radius: 2px;
    background: $white;
    transition: 0.3s;
}

.header__hamburger span:nth-child(2){
    margin-top: rem(6);
}

.header__hamburger span:nth-child(3){
    margin-top: rem(6);
}

.header__hamburger.--open span:nth-child(1){
    transform: rotate(45deg);
    margin-top: rem(5);
}

.header__hamburger.--open span:nth-child(1){
    transform: rotate(45deg);
    margin-top: rem(5);
}

.header__hamburger.--open span:nth-child(2){
    opacity: 0;
}

.header__hamburger.--open span:nth-child(3){
    transform: rotate(-45deg);
    margin-top: rem(-10);
}