@use "global" as *;

.card{
    display: flex;
    flex-direction: column;
    background: $white;
    color: $black;
    min-height: rem(400);
    position: relative;
    @include mq(md){
        min-height: rem(416);
        max-width: rem(350);
        width: 100%;
        &:hover{
            background: $gray;
            color: $white;
            transition: all  0.3s ease;
            opacity: 1;
        }
    }
}

.card__new{
    position: absolute;
    width: rem(60);
    height: rem(60);
    background-color: #FFBA6A;
    border-radius: 50%;
    top: rem(-16);
    left: rem(-15);
    z-index: 5;
    display: flex;
    justify-content: center;
    align-items: center;
}

.card--relation{
    @include mq(md) {
        max-width: rem(251);
        min-height: rem(265);
    }
}

.card + .card{
    margin-top: rem(24);
    @include mq(md){
        margin-top: 0;
    }
}

.card__img{
    width: 100%;
    position: relative;
}

.card__img::before{
    content: "";
    display: block;
    padding-top: calc(223 / 335 * 100%);
    @include mq(md){
        padding-top: calc(233 /350 * 100%);
    }
}

.card__img img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

.card__textbox{
    padding: 0 rem(23);
}

.card__title{
    font-size: rem(18);
    line-height: calc(35 / 18);
    letter-spacing: 0.05em;
    font-weight: $medium;
    margin-top: rem(4);
}

.card__text{
    font-size: rem(14);
    line-height: calc(26 / 14);
    letter-spacing: 0.07em;
    margin-top: rem(2);
    @include mq(md){
        margin-top: rem(5);
    }
}

.card__footer{
    font-size: rem(14);
    letter-spacing: 0.07em;
    margin-top: rem(7);
    margin-bottom: rem(16);
    line-height: calc(26 / 14);
    display: flex;
    justify-content: space-between;
    padding: 0 rem(23);
    @include mq(md){
        margin-top: auto;
    }
}

.card__category{
    background: $yellow;
    padding: 0 rem(5);
    color: $black;
    @include mq(md){
        margin-top: rem(10);
    }
}

.card__date{
    @include mq(md){
        margin-top: rem(10);
    }
}
