@use "global" as *;

.sub-overview{
  margin-top: rem(85);
  @include mq(md) {
    margin-top: rem(115);
  }
}

.sub-overview__item{
  display: flex;
  font-size: rem(13);
  padding: rem(17) 0;
  @include mq(md) {
    font-size: rem(16);
    padding: rem(30) 0;
  }
}

.sub-overview__item{
  border-bottom: solid 1px $white
}

.sub-overview__item dt{
  width: 15%;
  flex-shrink: 0;
}

.sub-overview__map{
  margin-top: rem(120);
  overflow: hidden;
  height: rem(300);
  @include mq(md) {
    margin-top: rem(80);
    height: rem(367);
  }
}

.sub-overview__map iframe{
  width: 100%;
  height: rem(600);
  transform: scale(1.2);
  margin-left: rem(-20);
  margin-top: rem(-150);
}

