@use "global" as *;

.breadcrumbs{
    margin-top: rem(12);
}

.breadcrumbs span{
    font-size: rem(16);
    letter-spacing: 0.04em;
}

.breadcrumbs span:not(:last-child):hover{
    border-bottom: solid 1px $white;
}

.breadcrumbs span.current-item:hover{
    border-bottom: none;
}

.breadcrumbs span + .breadcrumbs span{
    margin-left: rem(16);
}
