@use "global" as *;

.sub-news{
  margin-top: rem(90);
  @include mq(md) {
    margin-top: rem(70);
  }
}

.sub-news__item:not(:first-child){
  margin-top: rem(32);
}

.sub-news__inner{
  @include mq(md) {
    max-width: rem(860);
    width: 100%;
  }
}

.sub-news__item .news__header{
  @include mq(md) {
    width: 30%;
    display: inline-block;
  }
}

.sub-news__item .news__text{
  @include mq(md) {
    width: 60%;
    margin-right: 0;
  }
}