@use "global" as *;

.section-header{
    text-align: center;
    position: relative;
}

.section-header__title{
    font-size: rem(20);
    letter-spacing: 0.1em;
    font-weight: $regular;
    @include mq(md){
        font-size: rem(30);
        letter-spacing: 0.06em;
    }
}

.section-header__subtitle{
    position: absolute;
    font-family: $serif-font-family;
    color: #f9f871;
    font-size: rem(50);
    top: rem(-49);
    left: 50%;
    transform: translateX(-50%);
    opacity: 0.2;
    @include mq(md){
        font-size: rem(110);
        transform: initial;
        top: rem(-54);
        left: rem(90);
    }
}

.section-header__subtitle.--up{
    @include mq(md){
        top: rem(-90);
        left: 0;
    }
}

.section-header__subtitle--right{
    @include mq(md){
        left: auto;
        right: rem(90);
    }
}
