@use "global" as *;

.header-pcnav{
    height: inherit;
}

.header-pcnav__items{
    position: absolute;
    top: 50%;
    left: auto;
    right: 0;
    transform: translate(0, -50%);
    height: inherit;
    display: flex;
    flex-direction: row;
    text-align: center;
}

.header-pcnav__item + .header-pcnav__item{
    margin-top: 0;
}

.header-pcnav__item{
    height: inherit;
    margin-top: rem(-1);
}

.header-pcnav__item a{
    height: inherit;
    display: flex;
    align-items: center;
    position: relative;
}

.header-pcnav__item:not(:last-child){
    margin-left: rem(14);
    margin-right: rem(14);
}

.header-pcnav__item:nth-child(5){
    margin-left: rem(14);
    margin-right: rem(32);
}

.header-pcnav__item a:hover{
    opacity: 1;
}

.header-pcnav__item:not(:last-child) a:hover::before{
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    background: #FFEA2E;
    bottom: rem(10);
    left: 0;
}

.header-pcnav__item:last-child{
    padding: 0 rem(32);
    background: $white;
    color: $gray;
    position: relative;
    overflow: hidden;
    margin-left: rem(16);
}

.header-pcnav__item:last-child::before{
    position: absolute;
    content: "";
    background: $gray;
    width: 100%;
    height: inherit;
    top: 0;
    left: 0;
    transition: 0.3s ease-in-out;
    transform: translateX(-100%);
}

.header-pcnav__item:last-child:hover{
    color: $white;
    transition: 0.3s ease-in-out;
}

.header-pcnav__item:last-child:hover::before{
    transform: translateX(0);
}

.header-pcnav__item:last-child:hover::before{
    transform: translateX(0);
}