@use "global" as *;

.footer{
    padding: rem(51) 0 rem(60);
    @include mq(md){
        padding: rem(32);
    }
}

.footer__inner{
    @include mq(md){
        display: flex;
        justify-content: space-between;
    }
}

.footer__logo{
    width: rem(70);
    margin: 0 auto;
    @include mq(md){
        margin: 0;
    }
}

.footer__items{
    text-align: center;
    margin-top: rem(9);
    @include mq(md){
        display: flex;
        align-items: flex-start;
        margin-top: 0;
    }
}

.footer__item{
    font-size: rem(16);
    letter-spacing: 0.06em;
    margin-top: rem(7);
    @include mq(md){
        letter-spacing: 0.1em;
        margin-top: rem(2);
    }
}

.footer__item a{
    display: block;
    @include mq(md){
        height: inherit;
        display: flex;
        align-items: center;
        position: relative;
    }
}

.footer__item a:hover{
    @include mq(md){
        opacity: 1;
    }
}

.footer__item a:hover::before{
    @include mq(md){
        position: absolute;
        content: "";
        width: 100%;
        height: 1px;
        background: #FFEA2E;
        bottom: rem(-3);
        left: 0;
    }
}

.footer__item:not(:last-child){
    @include mq(md){
        margin-left: rem(16);
        margin-right: rem(16);
    }
}

.footer__item:last-child{
    @include mq(md){
        margin-left: rem(16);
        margin-right: 0;
    }
}

.copyright{
    font-size: rem(11);
    letter-spacing: 0.1em;
    text-align: center;
    margin-top: rem(32);
    @include mq(md){
        font-size: rem(15);
        letter-spacing: 0.08em;
        margin-top: rem(80);
    }
}