@use "global" as *;

.section-textbox{
    text-align: center;
    padding-bottom: rem(21);
    background: $gray;
    @include mq(md){
        text-align: left;
    }
}

.section-textbox__title{
    display: inline-block;
    font-size: rem(20);
    margin-top: rem(22);
    letter-spacing: 0.1em;
    font-weight: $regular;
    @include mq(md){
        font-size: rem(25);
        margin-top: rem(35);
        letter-spacing: 0.02em;
    }
}

.section-textbox__text{
    margin-top: rem(24);
    text-align: left;
    letter-spacing: 0.12em;
    line-height: 1.45;
    span{
        display: block;
    }
    @include mq(md){
        margin-top: rem(37);
        letter-spacing: 0.03em;
        line-height: calc(35 / 16);
        span{
            display: contents;
        }
    }
}