@use "global" as *;

.contact{
    background: $gray;
    padding-top: rem(67);
    padding-bottom: rem(64);
    @include mq(md){
        padding-top: rem(143);
        padding-bottom: rem(148);
        text-align: center;
    }
}

.contact__text{
    margin-top: rem(20);
    letter-spacing: 0.13em;
    line-height: 1.45;
}

.contact__btn{
    margin-top: rem(47);
    @include mq(md){
        margin: rem(60) auto 0;
        font-size: rem(20);
    }
}