@use "global" as *;

.content{
    position: relative;
}

.content::before{
    position: absolute;
    content: "";
    width: 100%;
    height: rem(679);
    background-image: linear-gradient(to right top, transparent 49.95%, #f9f871 50%, #f9f871 calc(50% + 1px), transparent calc(50.05% + 1px));
    background-size: cover;
    top: calc(-28 / 375 * 100vw);
    left: 0;
    z-index: -5;
    @include mq(md){
        width: 100%;
        height: rem(1280);
        top: calc(-106 / 1280 * 100vw);
    }
}

.content__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: rem(48);
    @include mq(md){
        margin: 0 calc(50% - 50vw);
        width: 100vw;
        margin-top: rem(33);
    }
}

.content__item{
    width: 50%;
    height: calc(168 / 375 * 100vw);
    font-size: rem(20);
    letter-spacing: 0.1em;
    position: relative;
    @include mq(md){
        width: 25%;
        height: calc(320 / 1280 * 100vw);
        font-size: rem(25);
        letter-spacing: 0.02em;
        overflow: hidden;
    }
}

.content__item p{
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    height: inherit;
    text-align: center;
    line-height: calc(168 / 375 * 100vw);
    @include mq(md){
        line-height: calc(320 / 1280 * 100vw);
    }
}

.content__item::before{
    @include mq(md){
        position: absolute;
        content: "";
        width: 100%;
        height: inherit;
        background: rgba(255,255,255,0.6);
        opacity: 0;
        transition: 0.3s;
    }
}

.content__item:hover::before{
    @include mq(md){
        opacity: 1;
    }
}
