@use "global" as *;

.header-nav{
    display: none;
    background: $black;
    color: $white;
    width: 100%;
    height: 100vh;
    padding: rem(10) rem(22) 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10;
    overflow: auto;
}

.header-nav__items{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    text-align: center;
}

.header-nav__item + .header-nav__item{
    margin-top: rem(31);
}

.header-nav__item{
    letter-spacing: 0.13em;
}