@use "global" as *;

.overview{
    position: relative;
}

.overview::before{
    position: absolute;
    content: "";
    width: 100%;
    height: rem(1398);
    background-image: linear-gradient(to right bottom, transparent 49.95%, #f9f871 50%, #f9f871 calc(50% + 1px), transparent calc(50.05% + 1px));
    background-size: cover;
    bottom: calc(-83 / 375 * 100vw);
    left: 0;
    z-index: -5;
    @include mq(md){
        width: 100%;
        height: rem(1284);
        top: calc(-270 / 1280 * 100vw);
    }
}

.overview__inner{
    @include mq(md){
        background: linear-gradient(-90deg, transparent 0%, transparent 30%, $gray 30%, $gray 100%);
        z-index: -3;
    }
}

.overview__itembox{
    @include mq(md){
        display: flex;
        flex-direction: row-reverse;
        margin-top: rem(33);
        justify-content: center;
        align-items: center;
        padding-top: rem(80);
        padding-bottom: rem(80);
    }
}

.overview__itembox>*:nth-child(2){
    @include mq(md){
        margin-right: rem(32);
    }
}

.overview__img{
    margin-top: rem(47);
    width: 100%;
    position: relative;
    @include mq(md){
        margin-top: 0;
        width: rem(557);
    }
}

.overview__img::before{
    content: "";
    display: block;
    padding-top: calc(261 / 375 * 100%);
    @include mq(md){
        padding-top: calc(389 / 557 * 100%);
    }
}

.overview__img img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

.overview__textbox{
    @include mq(md){
        width:rem(510);
        margin-top: rem(-24);
    }
}

.overview__btn{
    margin-top: rem(25);
    @include mq(md){
        margin-top: rem(48);
    }
}