@use "global" as *;

.card2{
    display: flex;
    flex-direction: column;
    @include mq(md){
        max-width: rem(525);
        width: 100%;
    }
}

.card2 + .card2{
    margin-top: rem(48);
    @include mq(md){
        margin-top: 0;
    }
}

.card2__box{
    position: relative;
}

.card2__img{
    width: 100%;
}

.card2__img img{
    aspect-ratio: 525 / 349;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.card2__category{
    background: $black;
    padding: rem(8) rem(20);
    position: absolute;
    top: 0;
    right: 0;
}

.card2__title{
    font-size: rem(20);
    line-height: calc(35 / 20);
    letter-spacing: 0.02em;
    font-weight: $medium;
    margin-top: rem(24);
    text-align: center;
    @include mq(md) {
        margin-top: rem(12);
    }
}

.cards2__items{
    @include mq(md){
        display: contents;
    }
}