@use "global" as *;

.works-article{
  margin-top: rem(90);
}

.works-article__inner{
  @include mq(md) {
    max-width: rem(850);
  }
}

.works-article__title{
  font-weight: 500;
}

.works-article__meta{
  margin-top: rem(32);
}

.works-article__meta span{
  margin-right: rem(24);
}

.works-article__term{
  border: solid 1px $white;
  padding: rem(8) rem(20);
  line-height: 1;
  letter-spacing: 0.1em;
  transition: 0.3s;
}

.works-article__term:hover{
  background: $white;
    color: $gray;
}

.works-article__slide-box{
  margin-top: rem(32);
  margin-left: calc(50% - 50vw);
  margin-right: calc(50% - 50vw);
  @include mq(md) {
    margin-left: inherit;
    margin-right: inherit;
  }
}

.works-article__slide{
  margin-bottom: rem(10);
}

.works-article__slide .swiper-slide{
  width: 100%;
  height: rem(251);
  @include mq(md) {
    height: rem(427);
  }
}

.works-article__slide .swiper-slide img{
  aspect-ratio: 375 / 251;
  width: 100%;
  height: 100%;
  object-fit: cover;
  @include mq(md) {
    aspect-ratio: 799 / 427;
  }
}

.works-article__slide-thumbnail .swiper-slide{
  width: rem(163);
  opacity: 0.1;
  @include mq(md) {
    width: calc((#{rem(800)} - #{rem(10)} * 4) / 5);
  }
}

.works-article__slide-thumbnail .swiper-slide-active {
  opacity: 1;
}

.works-article__slide .swiper-button-next,
.works-article__slide .swiper-button-prev{
  background-color: $black;
  border-radius: 50%;
  width: rem(30);
  height: rem(30);
  @include mq(md) {
    width: rem(50);
    height: rem(50);
  }
}

.works-article__slide .swiper-button-next::after,
.works-article__slide .swiper-button-prev::after{
  position: absolute;
  width: rem(12);
  height: rem(12);
  top: calc(50% - 3px); //ボーダーの太さ分を引く
  transform: rotate(45deg) translateY(-50%);
  color: $yellow;
  @include mq(md) {
    width: rem(20);
    height: rem(20);

  }
}

.works-article__slide .swiper-button-prev::after{
  border-bottom: solid 3px;
  border-left: solid 3px;
  right: rem(10);
  @include mq(md) {
    right: rem(20);
  }
}

.works-article__slide .swiper-button-next::after{
  border-top: solid 3px;
  border-right: solid 3px;
  right: rem(15);
  @include mq(md) {
    right: rem(25);
  }
}

.works-article__body{
  margin-top: rem(42);
  padding: 0 rem(20);
  @include mq(md) {
    margin-top: rem(80);
    padding: initial;
  }
}

.works-article__text-box{
  border: solid 1px $white;
  padding: rem(56) rem(10) rem(33);
  position: relative;
  @include mq(md) {
    padding: rem(44) rem(22) rem(33);
  }
}

.works-article__text-box:not(:first-child){
  margin-top: rem(54);
  @include mq(md) {
    margin-top: rem(70);
  }
}

.works-article__text-box dt{
  display: inline-block;
  border: solid 1px $white;
  background-color: $black;
  border-radius: 2em;
  font-size: rem(20);
  padding: rem(8) rem(24);
  position: absolute;
  top: -1em;
}

.works-article__text-box dd{
  line-height: calc(35 / 16);
  letter-spacing: 0.04em;
}