@use "global" as *;

.category__list{
    display: flex;
    flex-wrap: wrap;
    gap: rem(15) rem(24);
    max-width: calc(331 / 375 * 100vw);
    width: 100%;
    margin: auto;
    @include mq(md){
        gap: rem(20) rem(24);
    }
}

.category__item{
    display: inline-block;
    letter-spacing: 0.11em;
    height: inherit;
    cursor: pointer;
    transition: 0.3s;
    position: relative;
    &.--active{
        background: $white;
        color: $gray;
        padding: rem(2) rem(8) rem(3);
        @include mq(md){
            padding: rem(2) rem(10) rem(3);
        }
    }
    @include mq(md){
        padding: rem(2) rem(10) rem(3);
    }
    &:hover{
        @include mq(md){
            background: $white;
            color: $gray;
        }
    }
}


