@use "global" as *;

.cards{
    @include mq(md){
        display: grid;
        padding-left: 0;
        max-width: rem(1100);
        width: 100%;
        grid-template-columns: repeat(3, 1fr);
        gap: rem(48) rem(25);
        grid-template-rows: auto;
    }
}

.cards__items{
    @include mq(md){
        display: contents;
    }
}

.cards--relation{
    grid-template-columns: repeat(4,1fr);
    gap: rem(32) rem(24);
    margin-top: rem(80);
}