@use "global" as *;

.mv{
    height: 100vh;
    overflow: hidden;
    position: relative;
}

.mv--news{
    background: url(../images/mv_news_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_news_pc.jpg) no-repeat center center/cover;
    }
}

.mv--content{
    background: url(../images/mv_content_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_content_pc.jpg) no-repeat center center/cover;
    }
}

.mv--overview{
    background: url(../images/mv_overview_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_overview_pc.jpg) no-repeat center center/cover;
    }
}

.mv--archive,
.mv--taxonomy{
    background: url(../images/mv_blog_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_blog_pc.jpg) no-repeat center center/cover;
    }
}

.mv--archive-works,
.mv--taxonomy-works{
    background: url(../images/mv_works_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_works_pc.jpg) no-repeat center center/cover;
    }
}

.mv--contact{
    background: url(../images/mv_contact_sp.jpg) no-repeat center center/cover;
    width: 100%;
    @include mq(md) {
        background: url(../images/mv_contact_pc.jpg) no-repeat center center/cover;
    }
}

.mv__text{
    width: 100%;
    padding-right: rem(15);
    padding-left: rem(15);
    margin-right: auto;
    margin-left: auto;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-family: $base-font-family;
    font-weight: $bold;
    text-align: center;
    z-index: 10;
}

.mv__title{
    font-size: rem(28);
    letter-spacing: 0.02em;
    @include mq(md){
        font-size: rem(50);
        letter-spacing: 0.01em;
    }
}

.mv__subtitle{
    font-size: rem(20);
    margin-top: rem(14);
    @include mq(md){
        font-size: rem(25);
        margin-top: rem(5);
    }
}

.mv-sub{
    width: 100%;
    height: calc(425 / 375 * 100vw);
    position: relative;
    @include mq(md){
        height: rem(370);
    }
}

.mv-sub__text{
    font-size: rem(30);
    letter-spacing: 0.02em;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50% , -50%);
    @include mq(md){
        font-size: rem(35);
    }
}

.mv-none{
    height: 0;
    margin-top: rem(50);
}