@use "global" as *;

.blog-article{
  margin-top: rem(90);
}

.blog-article__title{
  font-weight: 500;
}

.blog-article__meta{
  margin-top: rem(32);
}

.blog-article__meta span{
  margin-right: rem(24);
}

.blog-article__term{
  border: solid 1px $white;
  padding: rem(8) rem(20);
  line-height: 1;
  letter-spacing: 0.1em;
  transition: 0.3s;
}

.blog-article__term:hover{
  background: $white;
    color: $gray;
}

.blog-article__body{
  margin-top: rem(32);
}
