@use "global" as *;

.works__inner{
    @include mq(md){
        background: linear-gradient(90deg, transparent 0%, transparent 30%, $gray 30%, $gray 100%);
    }
}

.works__itembox{
    @include mq(md){
        display: flex;
        margin-top: rem(33);
        justify-content: center;
        align-items: center;
        padding-top: rem(80);
        padding-bottom: rem(80);

    }
}

.works__itembox>*:first-child{
    @include mq(md){
        margin-right: rem(32);
    }
}

.works__imgbox{
    @include mq(md){
        max-width: rem(557);
        width: 100%;
        position: relative;
    }
}

.works__img{
    margin-top: rem(47);
    @include mq(md){
        margin-top: 0;
    }
}

.works__imgbox .swiper-pagination{
    @include mq(md){
        position: absolute;
        bottom: rem(-34);
    }
}

.works__img .slide-img{
    width: 100%;
    position: relative;
}

.works__img .slide-img::before{
    content: "";
    display: block;
    padding-top: calc(261 / 375 * 100%);
    @include mq(md){
        padding-top: calc(389 / 557 * 100%);
    }
}

.works__img .slide-img img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    object-fit: cover;
}

.works__textbox{
    margin-top: rem(-24);
    @include mq(md){
        max-width: rem(510);
        width: 100%;
    }
}

.works__btn{
    margin-top: rem(26);
    @include mq(md){
        margin-top: rem(48);
    }
}

.card__btn a{
    display: inline-block;
    height: inherit;
    padding: 5px;
    text-decoration: none;
    background: yellow;
}