@use "global" as *;

.button{
    width: rem(335);
    background: $gray;
    font-size: rem(20);
    color: $white;
    text-align: center;
    letter-spacing: 0.1em;
    border: solid 1px $white;
    display: block;
    margin: auto;
    padding: rem(9) 0;
    position: relative;
    overflow: hidden;
    z-index: 1;
    @include mq(md){
        width: rem(360);
        margin: auto 0;
        font-size: rem(16);
        transition: 0.3s;
        padding: rem(16) 0;
    }
}

.button::before{
    @include mq(md){
        position: absolute;
        content: "";
        background: $white;
        width:inherit;
        height: 100%;
        top: 0;
        left: 0;
        transition: 0.3s ease-in-out;
        transform: translateX(-100%);
        z-index: -1;
    }
}

.button:hover::before{
    @include mq(md){
        transform: translateX(0);
    }
}

.button:hover{
    @include mq(md){
        background: none;
        color: $gray;
    }
}

.button--news{
    @include mq(md){
        background: $white;
        color: $black;
        width: rem(134);
        padding: rem(2) 0;
        font-weight: $medium;
        text-align: center;
        cursor: pointer;
        position: relative;
        overflow: hidden;
        transition: 0.3s;
    }
}

.button--news::before{
    @include mq(md){
        position: absolute;
        content: "";
        background: $gray;
        width:inherit;
        height: 100%;
        top: 0;
        left: 0;
        transition: 0.3s ease-in-out;
        transform: translateX(-100%);
    }
}

.button--news:hover::before{
    @include mq(md){
        transform: translateX(0);
    }
}

.button--news:hover{
    @include mq(md){
        color: $white;
    }
}

.button--blog{
    margin-top: rem(23);
    @include mq(md){
        margin: rem(50) auto 0;
    }
}