@use "global" as *;

.cards2{
  @include mq(md){
      display: grid;
      padding-left: 0;
      max-width: rem(1100);
      width: 100%;
      grid-template-columns: repeat(2, 1fr);
      gap: rem(113) rem(48);
      grid-template-rows: auto;
  }
}

.cards2__items{
  @include mq(md){
      display: contents;
  }
}