@use "global" as *;

@keyframes zoomUp {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(1.15);
    }
}

.mv__swiper{
    height: inherit;
    .swiper-slide-active .mv__slide-img,
    .swiper-slide-duplicate-active .mv__slide-img,
    .swiper-slide-prev .mv__slide-img{
    animation: zoomUp 10s linear 0s 1 normal both;
    }
}

.mv__slide-img{
    height: inherit;
}

.mv__slide-img img{
    width: 100%;
    height: inherit;
    object-fit: cover;
}

.swiper-pagination{
    position: initial;
    margin-top: rem(-4);
    margin-bottom: rem(4);
    @include mq(md){
        margin-top: rem(7);
        margin-left: rem(-20);
    }
}

.swiper-pagination-bullet{
    background: #c4c4c4;
}

.swiper-pagination-bullet-active{
    background: #f9f871;
}