@use "global" as *;

.sub-content{
    margin-top: rem(85);
    @include mq(md){
        margin-top: rem(115);
    }
}

.sub-content-management{
    @include mq(md) {
        max-width: calc(568 / 1100) * 100%;
        width: 100%;
        margin-inline: auto;
    }
}

.sub-content-management__title{
    text-align: center;
    font-size: rem(20);
    font-weight: normal;
    line-height: calc(29 / 20);
    letter-spacing: 0.02em;
    @include mq(md) {
        font-size: rem(25);
        line-height: calc(35/25);
    }
}

.sub-content-management__text{
    line-height: calc(23 / 16);
    letter-spacing: 0.02em;
    margin-top: rem(24);
    @include mq(md) {
        line-height: calc(35 / 16);
    }
}

.sub-content-philosophy:first-child{
    margin-top: rem(80);
@include mq(md) {
    margin-top: rem(150);
}
}

.sub-content-philosophy:not(:first-child){
    margin-top: rem(48);
    @include mq(md) {
        margin-top: rem(105);
    }
}

.sub-content-philosophy__title{
    font-size: rem(20);
    font-weight: normal;
    line-height: calc(29 / 20);
    letter-spacing: 0.02em;
    margin-top: rem(24);
    text-align: center;
    @include mq(md) {
        font-size: rem(25);
        line-height: calc(35/25);
        margin-top: 0;
        text-align: left;
    }
}

.sub-content-philosophy__box{
    display: flex;
    flex-direction: column-reverse;
    @include mq(md) {
        flex-direction: row;
    }
}

.sub-content-philosophy:nth-child(even) .sub-content-philosophy__box{
    @include mq(md) {
        flex-direction: row-reverse;
    }
}

.sub-content-philosophy:nth-child(odd) .sub-content-philosophy__body{
    @include mq(md) {
        margin-right: rem(30);
    }
}

.sub-content-philosophy__img{
    @include mq(md) {
        max-width: calc(550 / 1100) * 100%;
        width: 100%;
        flex-shrink: 0;
    }
}

.sub-content-philosophy__img img{
    aspect-ratio: 550 / 367;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.sub-content-philosophy:nth-child(even) .sub-content-philosophy__img{
    @include mq(md) {
        margin-right: rem(30);
    }
}

.sub-content-philosophy__text{
    line-height: calc(23 / 16);
    letter-spacing: 0.02em;
    margin-top: rem(24);
    @include mq(md) {
        line-height: calc(35 / 16);
        margin-top: rem(40);
    }
}