@use "global" as *;

.wp-pagenavi{
    max-width: rem(600);
    margin: auto;
    margin-top: rem(80);
    display: flex;
    @include mq(md){
        margin-top: rem(105);
    }
}

.wp-pagenavi__inner{
    margin-inline: auto;
}

.wp-pagenavi .page-numbers{
    display: inline-block;
    border: solid 1px $white;
    line-height: 1;
    letter-spacing: 0.1em;
    transition: 0.3s;
    padding: rem(2) rem(6);
    margin-right: rem(16);
}

.wp-pagenavi .prev,
.wp-pagenavi .next{
    padding: rem(2) rem(12) rem(3);
}

.wp-pagenavi .prev{
    margin-right: rem(23);
}

.wp-pagenavi .next{
    margin-left: rem(7);
}

.wp-pagenavi .page-numbers:hover,
.page-numbers.current{
    background: $white;
    color: $gray;
}

.wp-pagenavi .navigation{
    margin-inline: auto;
}